











import { Component, Vue, Prop } from 'vue-property-decorator'

const INTRODUCTIONS = {
  'paper-vueify': 'paper-vueify is a vue adapter from paper.js, with paper\'s power of canvas drawing and vue\'s ability to data manipulation, it create a powerful data visualization and animation tool.',
  'animate-z': 'AnimateZ is a online animation editing tool, give you aibility to create code-free html canvas animation (project is still under refactoring, many features are missing).',
}

@Component
export default class Home extends Vue {
  @Prop({ required: true }) name!: string

  get intro() {
    return (INTRODUCTIONS as any)[this.name]
  }
}
